* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  padding-top: 60px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  box-shadow: 0 4px 4px #00000040;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__product-code--spaced {
  margin: 4px 0 16px;
}

.card__reviews-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  margin-block: 24px 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  width: 31px;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  width: 50px;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  padding-block: 12px;
  padding-inline: 69px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  padding: 11px 69px 11px 68px;
}

.stars {
  margin: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

html, body, div, h1, h2, h3, h4, h5, h6, p {
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

